<template>
	<!-- 顶部导航 -->
	<view class="expressTop">
		<view class="topNav">
			<u-dropdown :close-on-click-mask="true">
				<u-dropdown-item v-model="value1" title="公司" :height="420" :options="companyList" @change="refreshList()"></u-dropdown-item>
				<u-dropdown-item v-model="value2" title="项目" :height="420" :options="projectList" @change="refreshData()"></u-dropdown-item>
			</u-dropdown>
		</view>
		<view class="tabsTop" v-if="!isTop">
			<!-- <u-tabs :list="list" :is-scroll="false" :current="current" @change="change" active-color="#03958C"></u-tabs> -->
			<u-tabs :list="list" :is-scroll="false" :current="current" @change="change" height="100" bg-color="#fff" inactive-color="#666666" active-color="#03958C" :active-item-style="activeStyle"></u-tabs>
		</view>
	</view>
</template>

<script>
	import httpUtils from '@/utils/httpUtils.js';
	import Tools from '@/utils/tools.js';
	export default {
		props:['list','isTop'],
		data() {
			return {
				activeStyle: {
					fontSize:'36rpx'
				},
				value1:'',
				value2:'',
				isNull: false,
				companyList:[],
				projectList:[],
				// options: [{
				// 		label: '默认排序',
				// 		value: 1,
				// 	},
				// 	{
				// 		label: '距离优先',
				// 		value: 2,
				// 	},
				// 	{
				// 		label: '价格优先',
				// 		value: 3,
				// 	}
				// ],
				current:0
			}
		},
		created(){
			this._getCompanyList()
		},
		methods: {
			// 获取公司列表
			_getCompanyList(){
				let that = this
				httpUtils.sendHttpRequest('3003', Object.assign({}, ''), function (res) {
					Tools.hideLoading()
					if (res.data.code == '0') {
						that.companyList = res.data.data
						that.value1 = that.companyList.length > 0 ? that.companyList[0].value : ''
						that.$emit('getData',that.value1, that.value2)
						that._getProject()
					}
				})
			},
			// 获取项目列表
			_getProject(){
				let that = this
				httpUtils.sendHttpRequest('3004', Object.assign({}, {companyId:that.value1}), function (res) {
					Tools.hideLoading()
					if (res.data.code == '0') {
						that.projectList = res.data.data
						// that.value2 = that.projectList.length > 0 ? that.projectList[0].value : ''
						// if (that.value2 == '') {
						// 	that.$emit('clickPrj', that.value2)
						// }
					}
				})
			},
			// 点击公司回调
			refreshList(){
				this.value2 = ''
				this.$emit('getData',this.value1, this.value2)
				if(this.companyList.length > 1){
					this._getProject()
				}
			},
			// 选择项目回调
			refreshData(){
				this.$emit('clickPrj',this.value2)
			},
			closeDropdown() {
				this.$refs.uDropdown.close();
			},
			change(index) {
				this.current = index;
				this.$emit('getChange', this.current)
			}
		},
	}
</script>

<style lang="scss">
	// tabs顶部导航
	.expressTop{
		width: 100%;
		position: fixed;
		top: 0;
		z-index: 2;
		box-shadow: 0px 4rpx 32rpx 0px rgba(230, 230, 230, 0.5);
		.topNav {
			background-color: #fff;
		}
		.tabsTop{
			.u-tab-bar{
				background-color: #F5B00C!important;
			}
		}
	}
	// h5持有样式
	/* #ifdef H5 */
	.expressTop{
		top: 44px;
	}
	/* #endif */
</style>
